import React from 'react';
const Navbar = (props)=>{
    return(
        <nav>
            {props.user}
            
            {props.user?<button onClick={props.logout} >LogOut</button>:<div><button onClick={()=>{props.history.push('/signin')}} >Login</button>
            <button onClick={()=>{props.history.push('/signup')}} >SignUp</button></div>}
        </nav>
    )
}
export default Navbar;