import React, { Component } from 'react';
import './App.css';
import { Route, Switch, withRouter } from 'react-router-dom'
import SignIn from './containers/SignIn/Sigin';
import SignUp from './containers/SignUp/SignUp';
import Navbar from './components/Navbar/Navbar';
import { connect } from 'react-redux'
import Welcome from './containers/welcome/Welcome';
import authAction from './store/Actions/authAction';
import Template from './containers/Templates/Template';
import Aux from './hoc/Aux';

class App extends Component {
  componentDidMount() {
    // if (!this.props.user) {
    //   this.props.history.push('/signin')
    // }
  }
  render() {
    return (
      <div className="App">
        <Switch>
          {this.props.user ?
            <Aux>
              <Route path='/:username/:templateId/:edit?' render={(props) => {
                return(<Aux>
                  <Navbar {...props} user={this.props.user} logout={this.props.logout} history={this.props.history} />
                  <Template {...props} />
                </Aux>)
              }} />
              <Route path='/' exact render={(props) => {
                return(<Aux>
                  <Navbar {...props} user={this.props.user} logout={this.props.logout} history={this.props.history} />
                  <Welcome {...props}/>
                </Aux>)
              }} />
            </Aux>
            :
            <Aux>
              <Route path='/:username/:templateId/' component={Template} />
              <Route path='/signup' exact render={(props) => {
                return(<Aux>
                  <Navbar {...props} user={this.props.user} logout={this.props.logout} history={this.props.history} />
                  <SignUp {...props}/>
                </Aux>)
              }} />
              <Route path='/signin' exact render={(props) => {
                return(<Aux>
                  <Navbar {...props} user={this.props.user} logout={this.props.logout} history={this.props.history} />
                  <SignIn {...props} />
                </Aux>)
              }} />
              <Route path='/' exact render={(props) => {
                return(<Aux>
                  <Navbar {...props} user={this.props.user} logout={this.props.logout} history={this.props.history} />
                </Aux>)
              }} />

            </Aux>}

        </Switch>
      </div>
    );
  }
}
const mapStateToProps = state => ({
  user: state.authReducer.user,
})
const mapDispatchToProps = dispatch => ({
  logout: () => { dispatch(authAction.logout()) }
})

export default withRouter(connect(mapStateToProps, mapDispatchToProps)(App));
